<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process250"></a>

<p><br></p>
<p><span class="rvts17">看守者的怜悯（</span><span class="rvts50">The Watcher's Mercy</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:306,Y:777</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：看守（</span><span class="rvts20">The Warden</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">干掉矮人神殿中的被诅咒的杜娜守卫，并找看守领赏</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1385.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">在一处断桥口，可以遇到一个守卫，尝试与她交谈，她会告诉你在矮人神殿中，有守卫杜娜的一个被诅咒的骑士，她想让你干掉骑士。</span>
<br><span class="rvts20"></span>
<br><img width="100%" height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1386.jpg" display="block"><span class="rvts39"></span>
<br><span class="rvts19">矮人神殿的大门位于</span><span class="rvts20">X:325,Y:748</span><span class="rvts21">，进入矮人神殿。注意矮人神殿中到处都是石头矮人傀儡，到达神殿中心，会出现被诅咒的骑士。一直朝神殿左边走，穿过一大片陷阱区域可以再次遇到他。</span>
<br><span class="rvts21">尝试去说服他，可以使他瞬间崩溃死去。如果劝说失败，则需要一场战斗，当打败该骑士后，可以通过</span><span class="rvts20"></span><span class="rvts21">源力视野</span><span class="rvts20"></span><span class="rvts21">与他对话并吸取他的源力，最后返回接任务的地点领取奖励。</span>
<br><span class="rvts21">注意，被诅咒的骑士死亡后，所有的石头傀儡士兵都会灰飞烟灭。击杀石头傀儡士兵不会为我们带来任何经验。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



